import React, { Component } from "react";
import axios from 'axios';
//import { Link } from "react-router-dom";

class BbList extends Component {
    constructor(props) {
      super(props);
      this.state = {
        bblist: [],
      };
    }

    componentDidMount() {
        const url =
          "../lib/bbresp.json";
    
        fetch(url, {
          method: "GET",
          mode: "no-cors",
          headers: {
            "Access-Control-Allow-Credentials" : "true",
            "Access-Control-Allow-Origin": "*",
            "Content-Type": "application/json",
            "Accept": "application/json"
          }
        })
          .then(resp => {
              console.log(resp);
            return resp.json();
          })
          .then(data => {
              console.log(data)
            this.setState({
              bblist: data.offers
            });
          })
          .catch(err => {
            console.log(err);
          });
      }

      dbpost =  (e) =>{
        e.preventDefault();
        const obj = {
            bblisy: this.state.bblist
        };
        let config = {
            headers: { 'Content-Type': 'application/json' }
          };
        axios.post('/product/bestbuy', obj, config)
        .then(res => console.log(res.data));
      }

      render() {
        const BestBuyList = this.state.bblist;
        return (
            <div>
            <table>
            <tbody>
              <tr>
                <th>SKU</th>
                <th>Title</th>
                <th>Price</th>
                <th>Quantity</th>
              </tr>
              {BestBuyList.map(list => (
                <tr key={list.offer_id}>
                  <td>
                    {list.shop_sku}
                  </td>

                  <td>{list.product_title}</td>
                  <td>${list.price}</td>
                  <td>{list.quantity}</td>
                </tr>
              ))}
            </tbody>
          </table>
                <button onClick={this.dbpost}>Post API</button>
            </div>
        )
    }
}

export default BbList;